adena larga. Las resinas se utilizan para bases de dentaduras postizas, rebases y reparación de prótesis, dentaduras postizas parciales acrílicas provisionales, acondicionadores de tejidos y cubetas de impresión personalizadas. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-7",attrs:{"data-aos":"fade-left"}},[_c('p',[_vm._v("También tienen usos para retenedores de ortodoncia y dispositivos de movimiento de dientes removibles, protectores bucales para bruxismo, cubetas de flúor y blanqueamiento, carillas en coronas estéticas y restauraciones provisionales. Las resinas acrílicas especializadas se utilizan en el reemplazo estético de tejidos para recesiones gingivales graves y para la reconstrucción facial debido a traumatismos, cirugía o defectos de nacimiento. Las resinas acrílicas son especialmente útiles porque se les puede dar forma a cualquier contorno y personalizar el color para que coincida con el tono de los dientes, las encías o la piel. Las resinas acrílicas utilizadas en odontología a menudo se modifican mediante la adición de plastificantes, cauchos y rellenos para cambiar sus propiedades físicas.")])]),_c('div',{staticClass:"col-lg-3",attrs:{"data-aos":"fade-right"}},[_c('figure',[_c('img',{staticClass:"img-t m-auto",attrs:{"src":require("@/assets/curso/tema4/img03.png"),"alt":""}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-contenido",attrs:{"id":"t_4_2","data-aos":"zoom-out-right"}},[_c('h2',[_vm._v("4.2 Manipulación ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-lg-1"},[_c('figure',[_c('img',{staticClass:"img-t d-none d-lg-block",attrs:{"src":require("@/assets/curso/tema4/img1